<template>
    <div class="container">
        <h4>{{route}} {{origin}} 往 {{destination}}</h4>

        <div class="stop-list">
            <table class="table text-left">
                <thead>
                    <tr>
                        <th>站數</th>
                        <th>巴士站</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="stop in current_route_stop_list" :key="stop.id" @click="stop_click(stop.seq, this)">
                        <td class="td-seq">
                            <!--input type="image" class="star mr-2" width="25px" src="../assets/star.png" style="vertical-align: middle"-->
                            <!--img src="@/assets/star.png" width="25px" @click="star_click(stop.seq)"-->
                            <img src="@/assets/star.png" v-if="!(stop.star)" width="25px" @click="star_click(stop.seq)">
                            <img src="@/assets/starred.png" v-if="stop.star" width="25px" @click="star_click(stop.seq)">
                            <span> {{ stop.seq }}</span>
                        </td>
                        <td>
                            <div :class="[stop.seq == stop_seq_clicked ? `font-weight-bold`: '']">{{ stop.name_tc }}</div>
                            <div v-if="stop_seq_clicked == stop.seq">
                                <div v-if="!(stop.eta_1 || stop.eta_2 || stop.eta_3) && eta_loaded">
                                    暫時沒有預定班次
                                </div>
                                <div v-if="stop.eta_1">
                                    <span class="eta-time-diff">{{ stop.eta_1.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_1.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_1.rmk_tc"> ({{ stop.eta_1.rmk_tc }})</span>
                                </div>
                                <div v-if="stop.eta_2">
                                    <span class="eta-time-diff">{{ stop.eta_2.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_2.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_2.rmk_tc"> ({{ stop.eta_2.rmk_tc }})</span>
                                </div>
                                <div v-if="stop.eta_3">
                                    <span class="eta-time-diff">{{ stop.eta_3.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_3.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_3.rmk_tc"> ({{ stop.eta_3.rmk_tc }})</span>
                                </div>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import * as util from '../util/util'

export default {
    name: 'KMB_ETA',
    data(){
        return {
            route: this.$route.params.route,
            bound: this.$route.params.bound,
            service_type: this.$route.params.service_type,
            origin: "",
            destination: "",
            stop_list: [],
            route_list: [],
            route_stop_list: [],
            current_route_stop_list: [],
            stop_seq_clicked: 0,
            eta_loaded: false,
        }
    },
    computed: {
        star_image_src: function(){
            return this.current_route_stop_list.map(function(route_stop){
                if (route_stop.star){
                    return "../assets/star.png";
                }
                else {
                    return "../assets/star.png";
                }
            });
        }
    },
    mounted() {
        let that = this;
        
        // Load route destination
        (async () => {
            await util.getKMBRouteList()
            this.route_list = JSON.parse(localStorage.getItem("KMBRouteList"))
            let route_list_find = this.route_list.find(route_item => route_item.route == this.route && route_item.bound == this.bound && route_item.service_type == this.service_type);
            if (route_list_find){
                this.origin = route_list_find.orig_tc;
                this.destination = route_list_find.dest_tc;
            }
            else {
                // route not found
                location.href = "/KMB"
            }
        })();

        // Load stop list table        
        (async () => {
            await util.getKMBRouteStopList()
            that.loadStop();
        })();

        setInterval(() => { 
            if (this.eta_loaded){
                this.stop_click(this.stop_seq_clicked) ;
            }
        }, 10000);

    },
    methods: {
        loadStop: function(){
            let all_route_stop_list = JSON.parse(localStorage.getItem("KMBRouteStopList"));
            let stop_list = JSON.parse(localStorage.getItem("KMBStopList"));
            
            let route_stop_list = all_route_stop_list.filter(route_stop => route_stop.route == this.route && route_stop.bound == this.bound && route_stop.service_type == this.service_type);
            
            // map route_stop_list and stop_list
            this.current_route_stop_list = route_stop_list.map((e) => {
                let temp = stop_list.find(stop => stop.stop == e.stop)
                
                if (temp.stop){
                    e.name_tc = temp.name_tc;
                }
                return e;
            });
            
            
            // map current_route_stop_list and starred list
            // TODO
            let star_list = JSON.parse(localStorage.getItem("KMBRouteStopStar"));
            if (star_list){

                this.current_route_stop_list = this.current_route_stop_list.map((e) => {
                    
                    let found = star_list.find(star => (star.route == e.route &&
                                                        star.bound == e.bound &&
                                                        star.service_type == e.service_type &&
                                                        star.seq == e.seq)
                    );                                 

                    if (found){
                        e.star = true;
                    }
                    else {
                        e.star = false;
                    }
                    
                    return e;
                });               
            }

        },
        stop_click: function(stop_seq){
            // load ETA
            this.stop_seq_clicked = stop_seq;

            (async () => {
                let eta_data = await util.getKMBRouteETA(this.route, this.service_type);
                eta_data = eta_data.data;
                let eta_1 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == this.bound &&
                                                 eta.service_type == this.service_type &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 1);
                let eta_2 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == this.bound &&
                                                 eta.service_type == this.service_type &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 2);
                let eta_3 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == this.bound &&
                                                 eta.service_type == this.service_type &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 3);

                this.eta_loaded = true;

                // map eta to route stop list
                this.current_route_stop_list = this.current_route_stop_list.map((e) => {
                    if (eta_1.eta){
                        e.eta_1 = eta_1;
                        e.eta_1.time = util.formatTime(eta_1.eta);
                        e.eta_1.time_diff = util.etaTimeDifference(eta_1.eta);
                    }
                    if (eta_2.eta){
                        e.eta_2 = eta_2;
                        e.eta_2.time = util.formatTime(eta_2.eta);
                        e.eta_2.time_diff = util.etaTimeDifference(eta_2.eta);
                    }
                    if (eta_3.eta){
                        e.eta_3 = eta_3;
                        e.eta_3.time = util.formatTime(eta_3.eta);
                        e.eta_3.time_diff = util.etaTimeDifference(eta_3.eta);
                    }
                    
                    return e;
                });

                this.eta_loaded = true;

            })();
            
        },
        star_click: function(stop_seq){
            let that = this;

            // find the stop object
            let stop_item = {
                route: that.route,
                bound: that.bound,
                service_type: that.service_type,
                seq: stop_seq
            };

            // if found in star list, remove the starred item from the list, and change the image src
            // else add the item to star list, and change the image src

            let star_list = localStorage.getItem("KMBRouteStopStar");
            star_list = JSON.parse(star_list);

            if (!star_list){
                star_list = [];
            }
            let star_item_index = -1;

            // find the index of object with starred item
            for (let i = 0; i < star_list.length; i++){
                if ((star_list[i]).route == stop_item.route &&
                    (star_list[i]).bound == stop_item.bound &&
                    (star_list[i]).service_type == stop_item.service_type &&
                    (star_list[i]).seq == stop_item.seq){
                    star_item_index = i;
                    break;
                }
            }

            // starred --> unstar
            if (star_item_index > -1){
                star_list.splice(star_item_index, 1);

                // update current_route_stop_list star to false
                for (let i = 0; i < this.current_route_stop_list.length; i++){
                    let item = this.current_route_stop_list[i];
                    if (item.route == stop_item.route &&
                        item.bound == stop_item.bound && 
                        item.service_type == stop_item.service_type &&
                        item.seq == stop_item.seq){

                        (this.current_route_stop_list[i]).star = false;
                        break;
                    }
                }
            }
            else {  // unstar --> starred
                star_list.push(stop_item);

                // update current_route_stop_list star to true
                // update current_route_stop_list star to false
                for (let i = 0; i < this.current_route_stop_list.length; i++){
                    let item = this.current_route_stop_list[i];
                    if (item.route == stop_item.route &&
                        item.bound == stop_item.bound && 
                        item.service_type == stop_item.service_type &&
                        item.seq == stop_item.seq){
                        
                        (this.current_route_stop_list[i]).star = true;
                        break;
                    }
                }
            }

            localStorage.setItem("KMBRouteStopStar", JSON.stringify(star_list));

        },

    },

}

</script>

<style scoped>
.td-seq {
    width: 100px;
}
</style>
